import Api from '@/common/api';
export default {
	data() {
		return {
			paymentId: 0,
			paymentInfo: {}, // 支付单详情
			orderId: 0,
			status: false
		}
	},
	onLoad(options) {
		if (options.order) {
			let order  = JSON.parse(options.order)
			this.orderId = order.id
			this.paymentInfo = order
		}

	},
	methods: {}
};
